import React from "react";
import ReactDOM from "react-dom";
import { HashRouter } from "react-router-dom";
import App from "./App";
import "./index.css";

ReactDOM.render(
  <HashRouter baseline="/~rogeriomf">
    <App />
  </HashRouter>,
  document.getElementById("root")
);



